#contact-info {
  h4,
  h5 {
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  a,
  address {
    border-bottom: 0;
    font-family: $font-family-serif;
    font-size: 20px;
    text-transform: none;
  }

  .phone {
    font-family: $font-family-sans-serif;
    font-size: 18px;
  }
}



.distributors {
  align-items: stretch;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  //pseudo element so that rows that aren't filled will be aligned correctly
  &::after {
    height: 0;
    width: 30%;
    content: '';
  }

  .distributor {
    border-bottom: 2px solid $brand-primary;
    width: 100%;
    text-align: center;
    margin-bottom: 30px;
  }
}

.bordered {
  position: relative;
}

// Media Queries
// Small devices (landscape phones, 576px and up)
@media (min-width: $screen-sm-min) {
  .distributors {
    .distributor {
      width: 47%;
    }
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: $screen-md-min) {
  #contact-info {
    a,
    address {
      font-size: 1.8rem;
    }
  }
  .distributors {
    .distributor {
      width: 30%;
    }
  }
  .bordered {
    &::before,
    &::after {
      content: '';
      width: 30%;
      border-top: 1px solid $yellow;
      position: absolute;
      top: 50%;
    }

    &::before {
      left: 0;
    }

    &::after {
      right: 0;
    }
  }
}

// Large devices (desktops, 992px and up)
@media (min-width: $screen-lg-min) {}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: $screen-xl-min) {}
